"use client";
import Image from "next/image";
import React, { useState } from "react";
import homeImg from "@/public/assets/imgs/Home Page.jpg";
import Input from "@/app/components/ui/Input";
import { MdOutlineLocationOn } from "react-icons/md";
import { COLORS, TEXT_STYLES } from "@/app/constants";
import coffeeCupIcon from "@/public/assets/icons/coffee-cup.png"; // Assuming you have a coffee cup icon
import { FaStar } from "react-icons/fa"; // For the star icons
import userPic from "@/public/assets/imgs/IMG_7774-Photoroom.png"; // Assuming you have a default user image

const Page = () => {
  // state to track active tab
  const [activeTab, setActiveTab] = useState<"details" | "drinks">("details");

  const drinksData = [
    { name: "فانيليا سويت كوفي", price: "47.50", quantity: 1, id: 1 },
    { name: "فانيليا سويت كوفي", price: "47.50", quantity: 1, id: 2 },
    { name: "فانيليا سويت كوفي", price: "47.50", quantity: 1, id: 3 },
    { name: "فانيليا سويت كوفي", price: "47.50", quantity: 1, id: 4 },
  ];

  const totalCost = drinksData
    .reduce((sum, item) => sum + parseFloat(item.price) * item.quantity, 0)
    .toFixed(2);

  const reviews = [
    {
      name: "انس هشام",
      title: "مهندس برمجيات",
      rating: 5,
      comment:
        "كويس للشغل، النت سريع والمقاعد مريحة. في كهربا كتير، بس محتاجين يضيفوا حجز أونلاين للطاولات.",
      id: 1,
    },
    {
      name: "انس هشام",
      title: "مهندس برمجيات",
      rating: 5,
      comment:
        "كويس للشغل، النت سريع والمقاعد مريحة. في كهربا كتير، بس محتاجين يضيفوا حجز أونلاين للطاولات.",
      id: 2,
    },
  ];

  return (
    <div className="px-4 py-20 text-right">
      {/* Image */}
      <div className="flex justify-center">
        <Image src={homeImg} alt="Home" height={250} className="rounded-4xl" />
      </div>

      {/* Input */}
      <Input headline="كافيهينو" placeholder="المعادي - شارع النصر" />

      <MdOutlineLocationOn />

      {/* Tabs */}
      <div className="flex gap-4 mt-6">
        <div
          onClick={() => setActiveTab("details")}
          className={`cursor-pointer px-4 py-2  transition w-full text-center
            ${activeTab === "details" ? "border-b-2 font-bold" : ""}`}
          style={{
            ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H6,
            ...(activeTab === "details"
              ? {
                  color: COLORS.BRAND_PRIMARY,
                  borderColor: COLORS.BRAND_PRIMARY,
                }
              : {}),
          }}
        >
          تفاصيل الكافيه
        </div>

        <div
          onClick={() => setActiveTab("drinks")}
          className={`cursor-pointer px-4 py-2 transition w-full text-center
    ${activeTab === "drinks" ? "border-b-2 font-bold" : ""}`}
          style={{
            ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H6,
            ...(activeTab === "drinks"
              ? {
                  color: COLORS.BRAND_PRIMARY,
                  borderColor: COLORS.BRAND_PRIMARY,
                }
              : {}),
          }}
        >
          المشروبات
        </div>
      </div>

      {/* Tab Content */}
      <div className="mt-6">
        {activeTab === "details" && (
          <div>
            <Input
              headline="وصف الكافيه"
              placeholder="كُوفي شوب أنيق يجمع بين الدفء والحداثة، مصمم بديكور يجمع بين الطابع الكلاسيكي واللمسات العصرية. يتميز بأجواء هادئة تناسب العمل والدراسة، مع موسيقى خفيفة تزيد من استرخاء الزوار."
              isDesciption={true}
            />

            {/* Reviews Section */}
            <div className="mt-8">
              {reviews.map((review) => (
                <div
                  key={review.id}
                  className="flex flex-row-reverse gap-4 mb-6"
                >
                  <div className="">
                    <Image
                      src={userPic}
                      alt={review.name}

                      className="rounded-full w-12 h-12"
                    />
                  </div>
                  <div className="flex-1 ">
                    <div className="flex items-center justify-between flex-row-reverse">
                      <div>
                        <h4 className="font-bold text-lg">{review.name}</h4>
                        <p className="text-gray-500 text-sm">{review.title}</p>
                      </div>
                      <div className="flex ">
                        {[...Array(review.rating)].map((_, i) => (
                          <FaStar key={i} className="text-yellow-400" />
                        ))}
                      </div>
                    </div>
                    <p
                      className="mt-2 text-gray-700"
                      style={{
                        color: COLORS.GRAY,
                        ...TEXT_STYLES.READEX_PRO.MEDIUM.H7,
                      }}
                    >
                      {review.comment}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {activeTab === "drinks" && (
          <div>
            {drinksData.map((item) => (
              <div
                key={item.id}
                className="flex items-center justify-between mb-4"
              >
                <div className="flex items-center">
                  <span className="text-xl font-bold ml-2">{`X${item.quantity}`}</span>
                </div>
                <div className="flex items-center flex-grow">
                  <div className="flex flex-col text-right">
                    <span className="font-bold text-lg">{item.name}</span>
                    <span className="text-gray-500">{`${item.price} جنيه`}</span>
                  </div>
                  {/* <Image src={coffeeCupIcon} alt="Coffee cup" width={50} height={50} className="mr-2"/> */}
                </div>
              </div>
            ))}
            <div className="flex justify-between items-center mt-8 pt-4 border-t border-gray-300">
              <span className="text-xl font-bold">{`${totalCost} جنيه`}</span>
              <button className="bg-green-500 text-white font-bold py-3 px-6 rounded-full">
                اطلب الان
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Page;
